html {
    height: 100%; }
body {
    background: #fff;
    color: #000;
    @include smooth-font;
    @include hm-r;
    font-size: 22px;
    @include desktop-less-md {
        font-size: 20px; }
    @include tablet-less-sm {
        font-size: 18px; }
    &.is-fixed {
        overflow: hidden;
        width: 100%;
        height: 100%;
        .main {
            pointer-events: none; } }
    &.is-modal-open {
        overflow: hidden;
        position: fixed;
        width: 100%;
        height: 100%;
        .main {
            transform: none!important;
            transition: none; }
        .headers {
            position: relative;
            z-index: 0; } } }
strong, b {
    @include hm-sb; }
*::selection {
    color: #fff;
    background-color: $orange; }
a:not(.button) {
    transition: color .25s ease-in-out;
    cursor: pointer;
    &:hover {
        color: $orange; }
    &:active {
        color: $orange; } }

.out {
    //overflow: hidden
    //position: relative
 }    //z-index: 2

.main {
    // commented to make popup overlay on headers
    position: relative;
    z-index: 2;
    background: #fff;
    transition: transform .4s ease-in-out;
    padding-bottom: 1px; //fix falling out of margin https://monosnap.com/file/oFJbtd99I9scUE2EulaUcqgKxadVOC
    // overflow: hidden

    opacity: 0;
    animation-delay: 0.4s;
    animation-name: mainFadeAnim;
    animation-duration: 0.6s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards; }
@keyframes mainFadeAnim {
    0% {
        opacity: 0; }
    100% {
        opacity: 1; } }

body {
    &.ie {
        .main {
            //z-index: 1
            position: static; } } }

.headers {
    overflow: hidden; }

